.ProgressBar-container {
  background: black;
  height: 360px;
  width: 90px;
  border-radius: 25px;
  margin: 30px auto;
  position: relative;
  overflow: hidden;
}

.ProgressBar-percent {
  position: absolute;
  width: 100%;
  text-align: center;
  left: 0;
  bottom: 18px;
  font-weight: bold;
  line-height: 24px;
}

.ProgressBar-inner {
  width: 100%;
  background: blue;
  position: absolute;
  bottom: 0;
}