.App {
  text-align: center;
  background-color: #282c34;
  height: 100vh;
  color: white;
}

.App-header {
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 15px;
}

.App-input {
  background: none;
  color: white;
  border: 1px solid white;
  outline: none;
  margin-left: 5px;
  width: 40px;
  border-radius: 8px;
  padding: 8px 10px;
  font-size: 18px;
  text-align: center;
}

.App-container {
  padding: 10px;
}